.site-footer {
  background-image: linear-gradient(#fff, #ececece6);

  &__flex-container {
    display: flex;
    flex-wrap: wrap;
  }

  & h3 {
    margin-bottom: 10px;
  }

  &__icon-list {
  }

  &__icon {
    display: flex;
    margin-bottom: 15px;
  }

  &__icon p {
    margin: 0;
  }

  &__icon a {
    font-size: 16px;
  }

  &__icon img {
    margin-right: 10px;
  }

  &__col-one,
  &__col-two,
  &__col-three,
  &__col-four {
    width: 24%;
    margin-left: 1%;
    flex-wrap: wrap;
    min-width: 200px;
  }

  &__buttom-container {
    align-items: center;
    padding-bottom: 5px;
    padding-top: 5px;
    justify-content: center;
    background-color: $mainBlue;
    color: white;
    display: flex;
    justify-content: space-around;
    font-size: 14px;
    margin-top: 20px;
  }
  &__buttom-container p {
    margin: 0;
  }
}
